import PropTypes from 'prop-types';

export default PropTypes.shape({
  id: PropTypes.number,

  firstName: PropTypes.string.isRequired,
  lastName: PropTypes.string.isRequired,
  gender: PropTypes.string.isRequired,
  email: PropTypes.string.isRequired,
  phone: PropTypes.string.isRequired,
  addressLine1: PropTypes.string.isRequired,
  addressLine2: PropTypes.string,
  city: PropTypes.string.isRequired,
  country: PropTypes.string.isRequired,
});

export const formValues = PropTypes.shape({
  firstName: PropTypes.string,
  lastName: PropTypes.string,
  gender: PropTypes.string,
  email: PropTypes.string,
  phone: PropTypes.string,
  addressLine1: PropTypes.string,
  addressLine2: PropTypes.string,
  city: PropTypes.string,
  country: PropTypes.string,
});

export const formTouched = PropTypes.shape({
  firstName: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  lastName: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  gender: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  email: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  phone: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  addressLine1: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  addressLine2: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  city: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  country: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
});

export const formErrors = PropTypes.shape({
  firstName: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  lastName: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  gender: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  email: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  phone: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  addressLine1: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  addressLine2: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  city: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  country: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
});
