import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { formValues, formTouched, formErrors } from 'components/__types__/customer';
import { withFormik } from 'formik';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import { compose } from 'recompose';
import * as Yup from 'yup';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import ConfirmationDialogTrigger from 'components/common/ConfirmationDialogTrigger';

const styles = (theme) => ({
  root: {
    margin: theme.spacing(3),
  },
  textField: {
    width: '100%',
  },
});
class CustomerForm extends PureComponent {
  constructor(props) {
    super(props);
    this.handleConfirmationDialogAction = this.handleConfirmationDialogAction.bind(this);
  }

  handleConfirmationDialogAction(action) {
    const { onDelete, values } = this.props;
    switch (action) {
      case ConfirmationDialogTrigger.CONFIRM: {
        onDelete(values);
        break;
      }
      default:
        break;
    }
  }

  render() {
    const {
      classes,
      values,
      touched,
      errors,
      handleChange,
      handleBlur,
      handleSubmit: formikHandleSubmit,
      onDelete,
      onCancelEditing,
      isSubmitting,
      t,
    } = this.props;

    const getHelperText = (field) => (errors[field] && touched[field] ? errors[field] : '');

    const handleSubmit = (e) => {
      e.stopPropagation(); // avoids double submission caused by react-shadow-dom-retarget-events
      formikHandleSubmit(e);
    };

    return (
      <form onSubmit={handleSubmit} className={classes.root} data-testid="customer-form">
        <Grid container spacing={2}>
          <Grid item xs={12} sm={6}>
            <TextField
              id="customer-firstName"
              error={errors.firstName && touched.firstName}
              helperText={getHelperText('firstName')}
              className={classes.textField}
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.firstName}
              name="firstName"
              label={t('entities.customer.firstName')}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              id="customer-lastName"
              error={errors.lastName && touched.lastName}
              helperText={getHelperText('lastName')}
              className={classes.textField}
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.lastName}
              name="lastName"
              label={t('entities.customer.lastName')}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <InputLabel htmlFor="customer-gender">{t('entities.customer.gender')}</InputLabel>
            <Select
              native
              id="customer-gender"
              error={errors.gender && touched.gender}
              className={classes.textField}
              value={values.gender}
              name="gender"
              onChange={handleChange}
            >
              {/* eslint-disable-next-line jsx-a11y/control-has-associated-label */}
              <option value="" />
              <option value="MALE">MALE</option>
              <option value="FEMALE">FEMALE</option>
              <option value="OTHER">OTHER</option>
            </Select>
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              id="customer-email"
              error={errors.email && touched.email}
              helperText={getHelperText('email')}
              className={classes.textField}
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.email}
              name="email"
              label={t('entities.customer.email')}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              id="customer-phone"
              error={errors.phone && touched.phone}
              helperText={getHelperText('phone')}
              className={classes.textField}
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.phone}
              name="phone"
              label={t('entities.customer.phone')}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              id="customer-addressLine1"
              error={errors.addressLine1 && touched.addressLine1}
              helperText={getHelperText('addressLine1')}
              className={classes.textField}
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.addressLine1}
              name="addressLine1"
              label={t('entities.customer.addressLine1')}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              id="customer-addressLine2"
              error={errors.addressLine2 && touched.addressLine2}
              helperText={getHelperText('addressLine2')}
              className={classes.textField}
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.addressLine2}
              name="addressLine2"
              label={t('entities.customer.addressLine2')}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              id="customer-city"
              error={errors.city && touched.city}
              helperText={getHelperText('city')}
              className={classes.textField}
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.city}
              name="city"
              label={t('entities.customer.city')}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              id="customer-country"
              error={errors.country && touched.country}
              helperText={getHelperText('country')}
              className={classes.textField}
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.country}
              name="country"
              label={t('entities.customer.country')}
            />
          </Grid>
          {onDelete && (
            <ConfirmationDialogTrigger
              onCloseDialog={this.handleConfirmationDialogAction}
              dialog={{
                title: t('entities.customer.deleteDialog.title'),
                description: t('entities.customer.deleteDialog.description'),
                confirmLabel: t('common.yes'),
                discardLabel: t('common.no'),
              }}
              Renderer={({ onClick }) => (
                <Button onClick={onClick} disabled={isSubmitting}>
                  {t('common.delete')}
                </Button>
              )}
            />
          )}

          <Button onClick={onCancelEditing} disabled={isSubmitting} data-testid="cancel-btn">
            {t('common.cancel')}
          </Button>

          <Button type="submit" color="primary" disabled={isSubmitting} data-testid="submit-btn">
            {t('common.save')}
          </Button>
        </Grid>
      </form>
    );
  }
}

CustomerForm.propTypes = {
  classes: PropTypes.shape({
    root: PropTypes.string,
    textField: PropTypes.string,
    submitButton: PropTypes.string,
    button: PropTypes.string,
    downloadAnchor: PropTypes.string,
  }),
  values: formValues,
  touched: formTouched,
  errors: formErrors,
  handleChange: PropTypes.func.isRequired,
  handleBlur: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  onDelete: PropTypes.func,
  onCancelEditing: PropTypes.func,
  isSubmitting: PropTypes.bool.isRequired,
  t: PropTypes.func.isRequired,
  i18n: PropTypes.shape({ language: PropTypes.string }).isRequired,
};

CustomerForm.defaultProps = {
  onCancelEditing: () => {},
  classes: {},
  values: {},
  touched: {},
  errors: {},
  onDelete: null,
};

const emptyCustomer = {
  firstName: '',
  lastName: '',
  gender: '',
  email: '',
  phone: '',
  addressLine1: '',
  addressLine2: '',
  city: '',
  country: '',
};

const validationSchema = Yup.object().shape({
  firstName: Yup.string().required(),
  lastName: Yup.string().required(),
  gender: Yup.string().required(),
  email: Yup.string()
    .required()
    .matches(/^[^@\s]+@[^@\s]+\.[^@\s]+$/),
  phone: Yup.string().required(),
  addressLine1: Yup.string().required(),
  addressLine2: Yup.string(),
  city: Yup.string().required(),
  country: Yup.string().required(),
});

const formikBag = {
  mapPropsToValues: ({ customer }) => customer || emptyCustomer,

  enableReinitialize: true,

  validationSchema,

  handleSubmit: (values, { setSubmitting, props: { onSubmit } }) => {
    onSubmit(values);
    setSubmitting(false);
  },

  displayName: 'CustomerForm',
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withTranslation(),
  withFormik(formikBag)
)(CustomerForm);
